import axios from "axios";
import React, { useState, useEffect, useRef } from "react";
import { styled } from "styled-components";
import markerIcon from "../assets/image/marker.png";
import starIcon from "../assets/image/starIcon.png";

const AddDestinationMap = () => {
  // const [myLocation, setMyLocation] = useState({});
  const [top10List, setTop10List] = useState([]);
  const [markerArr, setMarkerArr] = useState([]);
  const [placeInfo, setPlaceInfo] = useState({});
  const { naver } = window;

  const addrReplace = (place) => {
    if (place !== undefined) {
      const splitAddr = place.split(" ");
      const Addr = splitAddr.slice(0, 2).join(" ");
      return Addr;
    }
  };

  useEffect(() => {
    const getData = async () => {
      await axios({
        method: "post",
        url: `${process.env.REACT_APP_API_URL}/place/recommand`,
        data: {
          curPage: 1,
        },
      })
        .then((res) => {
          console.log(res.data.dataList);
          setTop10List(res.data.dataList);
          const copyArr = [...res.data.dataList];
          const result = copyArr.map((el) => {
            return { lat: el.mapy, lng: el.mapx };
          });
          setMarkerArr(result);
          setPlaceInfo(res.data.dataList[0]);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, []);

  useEffect(() => {
    document.addEventListener("message", (e) => {
      console.log(e.data);
    });
  });

  // 현재 위치 받아오기
  // useEffect(() => {
  //   if (navigator.geolocation) {
  //     navigator.geolocation.getCurrentPosition((position) => {
  //       setMyLocation({
  //         latitude: position.coords.latitude,
  //         longitude: position.coords.longitude,
  //       });
  //     });
  //   } else {
  //     window.alert("현재위치를 알수 없습니다.");
  //   }
  // }, []);
  // console.log(myLocation);

  // 주변마커 오버레이 클릭 이벤트 적용하기
  useEffect(() => {
    const map = new naver.maps.Map("map", {
      center: new naver.maps.LatLng(36.5, 127.6),
      zoomControl: true,
      zoom: 7,
    });

    // const currentMarker = new naver.maps.Marker({
    //   position: new naver.maps.LatLng(37.4859, 126.997865),
    //   map,
    //   // icon: {
    //   //   url: "../assets/image/marker.png",
    //   //   size: new naver.maps.Size(50, 52),
    //   //   origin: new naver.maps.Point(0, 0),
    //   //   anchor: new naver.maps.Point(25, 26),
    //   // },
    // });

    // 주변 마커 나타내기
    const markers = [];
    // const infowindows = [];
    // const contentTags =
    //   '<div class="naver-container"><p class="ptag">여깁니다</p><span class="spantag">맞아요</span></div>';

    // 반복문을 통해 찍어준다
    for (let i = 0; i < markerArr.length; i += 1) {
      const otherMarkers = new naver.maps.Marker({
        position: new naver.maps.LatLng(markerArr[i].lat, markerArr[i].lng),
        map,
        // 원하는 이미지로 마커 커스텀
        icon: {
          url: markerIcon,
          size: new naver.maps.Size(50, 52),
          scaledSize: new naver.maps.Size(50, 52),
          origin: new naver.maps.Point(0, 0),
          anchor: new naver.maps.Point(25, 26),
        },
      });

      // const infowindow = new naver.maps.InfoWindow({
      //   content: contentTags,
      //   borderWidth: 1,
      //   anchorSize: new naver.maps.Size(10, 10),
      //   pixelOffset: new naver.maps.Point(10, -10),
      // });

      markers.push(otherMarkers);
      // infowindows.push(infowindow);
    }

    naver.maps.Event.addListener(map, "idle", () => {
      updateMarkers(map, markers);
    });

    const updateMarkers = (isMap, isMarkers) => {
      const mapBounds = isMap.getBounds();
      let marker;
      let position;

      for (let i = 0; i < isMarkers.length; i += 1) {
        marker = isMarkers[i];
        position = marker.getPosition();

        if (mapBounds.hasLatLng(position)) {
          showMarker(isMap, marker);
        } else {
          hideMarker(marker);
        }
      }
    };

    const showMarker = (isMap, marker) => {
      marker.setMap(isMap);
    };

    const hideMarker = (marker) => {
      marker.setMap(null);
    };

    const getClickHandler = (seq) => {
      return () => {
        const marker = markers[seq];
        // const infoWindow = infowindows[seq];
        console.log(seq);
        setPlaceInfo(top10List[seq]);

        // if (infoWindow.getMap()) {
        //   infoWindow.close();
        // } else {
        //   infoWindow.open(map, marker);
        // }
      };
    };

    for (let i = 0; i < markers.length; i += 1) {
      naver.maps.Event.addListener(markers[i], "click", getClickHandler(i));
    }
  }, [top10List]);
  return (
    <Container>
      <div id="map" style={{ width: "100%", display: "flex", flex: 1 }} />
      <InfoContainer>
        <InfoImg src={placeInfo.firstimage2} />
        <InfoTextContainer>
          <InfoTextTop>
            <TitleArea>
              {placeInfo.title}
              <StarIcon src={starIcon} />5
            </TitleArea>
            <CategoryArea>{placeInfo.describe}</CategoryArea>
          </InfoTextTop>
          <InfoTextBottom>
            <BottomLeft>{addrReplace(placeInfo.addr1)}</BottomLeft>
            <AddBtn>등록</AddBtn>
          </InfoTextBottom>
        </InfoTextContainer>
      </InfoContainer>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex: 1;
  flex-direction: column;
`;
const MapContainer = styled.div`
  width: 100%;
  display: flex;
  flex: 1;
`;
const InfoContainer = styled.div`
  display: flex;
  width: 100%;
  height: 120px;
  padding: 10px 20px;
  flex-direction: row;
  background-color: #fff;
  border-top: 1px solid #d9d9d9;
`;
const InfoImg = styled.img`
  width: 100px;
  height: 100px;
  border-radius: 8px;
  margin-right: 10px;
`;
const InfoTextContainer = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`;
const InfoTextTop = styled.div`
  display: flex;
  width: 100%;
  height: 45px;
  flex-direction: column;
  justify-content: space-between;
`;
const InfoTextBottom = styled.div`
  display: flex;
  width: 100%;
  height: 42px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;
const TitleArea = styled.div`
  display: flex;
  height: 26px;
  flex: 1;
  font-size: 18px;
  font-weight: 500;
  line-height: 26.06px;
  color: #000;
  align-items: center;
`;
const CategoryArea = styled.div`
  display: flex;
  font-size: 12px;
  font-weight: 300;
  line-height: 17.38px;
  color: #25b1fa;
`;
const StarIcon = styled.img`
  width: 18px;
  height: 18px;
  margin-left: 10px;
  margin-right: 6px;
`;
const BottomLeft = styled.div`
  display: flex;
  flex: 1;
`;
const AddBtn = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 30px;
  border: 1px solid #25b1fa;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 17.38px;
  color: #25b1fa;
  cursor: pointer;
`;

export default AddDestinationMap;
